import CONST from 'src/assets/const';
import React from 'react';
import TextField from '@material-ui/core/TextField';
import 'src/css/pages/Main.css';

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileName: null,
            fileType: null
        }
    }

    handleUpload = (e) => {
        console.log("file change", e.target.files);
        const reader = new FileReader();
        const file = e.target.files[0];

        reader.onloadend = () => {
            console.log("onloaded")
            this.setState({
                fileName: file.name,
                fileType: file.type
            });
        };
        reader.readAsDataURL(file);
    }

    changeFile = (e) => {
        console.log("changeFile");
        this.setState({ fileName: e.target.value })
    }

    render() {
        return (
            <div className="Main BorderBody">
                <div className="Title BorderPrimary SizeRes20 Bold">{CONST.TEXT.MAIN_TITLE}</div>
                <div className="Item BorderWarning">
                    <div className="Upload">
                        <div className="SubItem">
                            <input className="Hidden" id="file-upload" type="file" onChange={this.handleUpload} />
                            <button className="BackColorPrimary ColorWhite">
                                <label htmlFor="file-upload" className="Button">
                                    {CONST.TEXT.FILE} {CONST.BUTTON.UPLOAD}
                                </label>
                            </button>
                        </div>
                        <div className="SubItem">
                            <div className="ColorBody Left"> {CONST.TEXT.FILE}{CONST.TEXT.NAME_}</div>
                            <TextField
                                id="file-name"
                                style={{ minWidth: 120 }}
                                label={CONST.PLACEHOLDER.FILE}
                                value={this.state.fileName || ''}
                                onChange={this.changeFile}
                            />
                            {/* <div className="ColorExtra Right BorderExtra Size14">{this.state.selectedFile.name}</div> */}
                        </div>
                        <div className="SubItem">
                            <div className="ColorBody Left">{CONST.TEXT.TYPE}</div>
                            <div className="ColorExtra Right BorderExtra Size14">{this.state.fileType}</div>
                        </div>

                    </div>
                    <div></div>
                </div>
            </div>
        );
    }
}

export default Main;
